(function () {
    'use strict';
    var angular = require('angular');
    require('./lead-gen-ack.component.scss');

    var leadGeneration = angular.module('app').component('leadGenAck', {
        template: require('./lead-gen-ack.component.html'),
        controller: ['$state', '$anchorScroll', 'appState', 'analyticService', function ($state, $anchorScroll, appState, analyticService) {
            var vm = this;

            vm.$onInit = function () {
                if ($state.params.ackData) {
                    vm.refNumber = $state.params.ackData.refNumber;
                    vm.isStaffJourney = appState.isStaffJourney();
                    analyticService.trackPageLoad(analyticService.pageList.leadGenerationAcknowledgement);
                    $anchorScroll();
                } else {
                    $state.go("app.journey.s1-cards");
                }
            };

            vm.exit = function () {
                analyticService.buttonList.buttonClick.event_content = "exit";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
                // $state.go("app.journey.s1-cards");
                window.location.href = "http://www.hsbc.co.in/";
            }

            //vm.closeError = function () {
            //  vm.errorMessage = false;
            //};
        }]
    });

    module.exports = leadGeneration.name;
})();



